<template>
  <section>
    <div class="level">
      <figure class="image is-96x96 mr-1">
        <img src="../../../public/img/man.png" />
      </figure>
      <div>
        <p class="title is-4 margin-05">{{ heroinfo.name }}</p>
        <p>
          上次登陆:
          <b class="has-text-cblue-2">{{ heroinfo.lastlogintime }}</b>
        </p>
        <p>
          登陆IP:
          <b class="has-text-cblue-2">{{ heroinfo.lastip }}</b>
        </p>
      </div>
    </div>
    <hr class="margin-0" />
    <div class="level">
      <div class="tile">
        <div class="tile is-child is-cgreen-3 notification" style="zoom:65%">
          <h1 class="title is-2 has-text-centered">
            <b>{{ heroinfo.completed }}</b>
          </h1>
          <p class=" has-text-centered">
            已完成
          </p>
        </div>
        <div class="tile is-child is-cred-2 notification" style="zoom:65%">
          <h1 class="title is-2 has-text-centered">
            <b>{{ heroinfo.exec }}</b>
          </h1>
          <p class="has-text-centered">
            待执行
          </p>
        </div>
      </div>
    </div>
    <hr class="margin-0" />
    <div>
      <apexchart :options="linechart.Options" :series="linechart.series" />
    </div>
  </section>
</template>

<script>
import { TASKSTATUS_CN, makeEemu } from '../../scripts/internal'
import { personalactive, hero } from '../../scripts/welcome'
export default {
  name: 'Hero',
  components: {},
  props: {},
  data() {
    return {
      /**
       * line 图表
       */
      linechart: {
        series: [],
        Options: {
          chart: {
            height: 100,
            width: 100,
            type: 'line',
            toolbar: {
              show: false
            }
          },
          dataLabels: {
            enabled: false
          },
          stroke: {
            curve: 'smooth'
          },
          xaxis: {
            categories: []
          }
        }
      },
      heroinfo: {},
      personal5days: []
    }
  },

  mounted() {
    this.init()
    this.initpro5days()
    // this.inithero()
  },
  methods: {
    init() {
      const tenum = makeEemu(TASKSTATUS_CN)
      tenum.splice(0, 1)
      tenum.splice(1, 1)
      this.linechart.series = [] // 初始化
      this.linechart.Options.xaxis.categories = [] // 初始化
      tenum.forEach(ttype => {
        const template = {
          name: ttype,
          data: []
        }
        this.linechart.series.push(template)
      })
    },

    /**
     * 处理图表数据
     * 按照每日分拆新增改查
     * TODO: 数据从后端是全英文的，所以还需要再做二次处理
     */
    initpro5days() {
      let persontemplate
      // 处理数据
      personalactive()
        .then(result => {
          persontemplate = result
        })
        .catch(demo => {
          persontemplate = demo
        })
        .finally(() => {
          persontemplate.forEach(element => {
            this.linechart.Options.xaxis.categories.push(element.日期)
            this.linechart.series[0].data.push(element.新建)
            this.linechart.series[1].data.push(element.已完成)
            this.linechart.series[2].data.push(element.执行中)
            this.linechart.series[3].data.push(element.挂起)
            this.linechart.series[4].data.push(element.冻结)
          })
          this.inithero()
        })
    },

    /**
     * 获取hero信息
     */
    inithero() {
      let herotemplate
      hero()
        .then(result => {
          herotemplate = result
        })
        .catch(demo => {
          herotemplate = demo
        })
        .finally(() => {
          this.heroinfo = herotemplate
        })
    }
  }
}
</script>

<style scoped>
.margin-05 {
  margin-bottom: 0.5rem;
}

.margin-0 {
  margin: 0rem;
}
</style>
