<template>
  <div>
    <div>
      <apexchart
        ref="barchart"
        type="bar"
        :options="timelinechart.chartOptions"
        :series="timelinechart.series"
      ></apexchart>
    </div>
    <div>
      <p class="pb-4">工作台(TOP3)</p>
      <div class="level">
        <div v-for="(item, index) in worktabletask" :key="index" class="level-item">
          <vue-ellipse-progress :progress="item.value" :size="100" color="#3e64ff">
            <span slot="legend-value">/ {{ item.total }}</span>
            <span slot="legend-caption">{{ item.type.toUpperCase().slice(3) }}</span>
          </vue-ellipse-progress>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { worktabletask } from '../../scripts/welcome'
import { getYestdayTaskInfo } from '../../scripts/welcome'
export default {
  name: 'Activation',
  data() {
    return {
      /**
       * 工作台任务简介
       */
      worktabletask: [],
      /**
       * 时间线数据以及配置
       * NOTICE: 这里改成bar，
       */
      timelinechart: {
        series: [
          {
            data: []
          }
        ],
        chartOptions: {
          chart: {
            type: 'bar',
            height: 350,
            toolbar: {
              show: false
            }
          },
          plotOptions: {
            bar: {
              // borderRadius: 4,
              horizontal: true,
              distributed: true,
              dataLabels: {
                position: 'top'
              }
            }
          },
          legend: {
            show: false
          },
          dataLabels: {
            enabled: true,
            textAnchor: 'start',
            style: {
              colors: ['#333333']
            },
            formatter: function(val, opt) {
              return opt.w.globals.labels[opt.dataPointIndex] + ': ' + val + '秒'
            },
            offsetX: 0,
            dropShadow: {
              enabled: false
            }
          },
          colors: [
            '#0278ae',
            '#51adcf',
            '#a5ecd7',
            '#e8ffc1',
            '#e93b81',
            '#f5abc9',
            '#ffe5e2',
            '#b6c9f0',
            '#867ae9',
            '#185adb'
          ],
          stroke: {
            width: 0,
            colors: ['#fff']
          },
          xaxis: {
            categories: []
          },
          yaxis: {
            labels: {
              show: false
            }
          }
        }
      }
    }
  },
  mounted() {
    this.init()
    this.seriesFunc()
  },
  methods: {
    init() {
      // 获取工作台内前三的大任务的完成总数比
      worktabletask()
        .then(result => {
          this.worktabletask = result
        })
        .catch(demo => {
          this.worktabletask = demo
        })
    },
    /**
     * 数据处理
     */
    seriesFunc() {
      this.timelinechart.series[0].data = []
      this.timelinechart.chartOptions.xaxis.categories = []
      getYestdayTaskInfo()
        .then(result => {
          const jsonresult = JSON.parse(result)
          jsonresult.forEach(element => {
            this.timelinechart.series[0].data.push(element.usetime)
            this.timelinechart.chartOptions.xaxis.categories.push(element.taskname)
          })
        })
        .catch(demo => {
          demo.forEach(element => {
            this.timelinechart.series[0].data.push(element.usetime)
            this.timelinechart.chartOptions.xaxis.categories.push(element.taskname)
          })
        })
        .finally(() => {
          // 更新数据
          // NOTICE: apexchart是需要使用方法更新数据和配置，直接修改data内值没有用
          this.$refs.barchart.updateSeries([
            {
              data: this.timelinechart.series[0].data
            }
          ])
          this.$refs.barchart.updateOptions({
            xaxis: {
              categories: this.timelinechart.chartOptions.xaxis.categories
            }
          })
        })
    }
  }
}
</script>

