<template>
  <div class="tile">
    <card-component class="tile is-child" title="任务总览" icon="file-tree">
      <b-tabs v-model="activetab" type="is-boxed" size="is-small" class="margin-05" @input="changeTab">
        <b-tab-item v-for="(item, index) in persionaltasknav" :key="index" :value="item.name" class="padding-05">
          <template slot="header">
            <b-icon :icon="item.data.icon" />
            <span>
              {{ item.data.label }}
            </span>
            <b-tag>{{ item.data.value }}</b-tag>
          </template>
          <personal-task-table
            ref="ptt"
            :alldata="activetabtasklist"
            :showtaskid="false"
            :width="200"
            @updateTaskData="updateTaskData"
            @changeSel="changeSel"
          />
        </b-tab-item>
      </b-tabs>
    </card-component>
    <card-component class="tile is-child" title="执行日志" icon="blogger">
      <exec-log :loglist="logs" />
    </card-component>
  </div>
</template>

<script>
/**
 * UPDATE: 2020-02-09 home页面中展示被本人任务的所有数据
 * NOTICE: 和AYO的展示差不多，左侧是任务列表,后侧是API测试(其实这里应该可以更换查看)
 */
import CardComponent from '@/components/Card/CardComponent'
import PersonalTaskTable from '@/components/Table/PersonalTaskTable'
import ExecLog from '@/components/Table/ExecLog'
import { persionaltasknav, totaltask, execloglist } from '../../scripts/common'
import { TASKSTATUS_EN } from '../../scripts/internal'
export default {
  name: 'TaskTile',
  components: {
    CardComponent,
    PersonalTaskTable,
    ExecLog
  },
  data() {
    return {
      /**
       * 个人任务nav
       * 包含数量
       * 包含按照类型分类的navtitle
       */
      persionaltasknav: persionaltasknav,
      /**
       * 活动的tab内容
       */
      activetabtasklist: [],
      /**
       *所有数据
       */
      totaltasklist: [],
      /**
       * 活动的table sheet
       */
      activetab: '',
      /**
       * 已选择的task
       */
      selectedtask: {},
      /**
       * 执行日志
       */
      logs: []
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    /**
     * 初始化
     */
    init() {
      this.totaltasklist = totaltask()
      this.activetab = TASKSTATUS_EN.EXEC
      this.changeTab(TASKSTATUS_EN.EXEC)
      this.logs = execloglist()
    },

    /**
     * 改变tab sheet
     */
    changeTab(value) {
      // console.log(value)
      // 设置活动的任务tab的为空
      this.activetabtasklist = []
      // 根据状态分拆到各个tab内，当tab进行更改时执行
      if (value === TASKSTATUS_EN.TOTAL) {
        this.activetabtasklist = this.totaltasklist
      } else {
        for (let index = 0; index < this.totaltasklist.length; index++) {
          const element = this.totaltasklist[index]
          if (element.taskstatus.toLowerCase() === value) {
            this.activetabtasklist.push(element)
          }
        }
      }
    },
    /**
     * 从子组件传递至上部组件的数据
     */
    updateTaskData(data) {
      /** 替换数据 */
      this.totaltasklist[data.id] = data
      // 再刷新table
      this.changeTab(this.activetab)
    },

    /**
     * 子组件新选择了task，更新
     */
    changeSel(data) {
      this.selectedtask = data
    }
  }
}
</script>

<style scoped>
.margin-0 {
  margin: 0rem;
}

.margin-05 {
  margin: 0.5rem;
}

.padding-05 {
  padding: 0.5rem;
}
</style>
