import dateFormat from 'dateformat'
import store from '../store/index'
import { nowdate } from './common'
import { makeEemu, MESSAGETYPE_EN } from './internal'
import { getFunc } from './internal/httpFunc'
/**
 * 登录后，基本用户信息
 * COM: hometile.vue
 * STATUS: 2021-05-18   NEW完成修改
 */
const hero = () => {
  // 默认数据
  const defaultvalue = {
    name: '冯涛(Nate Feng)',
    lastlogintime: '2020-10-15T17:41:12',
    lastip: '127.0.0.1',
    exec: 55,
    completed: 12
  }
  return new Promise(function(resolve, reject) {
    // 含有后台的时候，可以直接从vuex内获取部分信息
    const user = store.state.common.activeusers
    // 再从远程获取对应的人员已完成和正在完成的任务
    getFunc('/query/taskonhero', { userid: user.id })
      .then(res => {
        if (res.resstatus === true) {
          // 赋值
          const result = {
            name: user.unickname,
            lastlogintime: user.ulastlogin,
            lastip: user.uhostname,
            exec: res.resbody.exec,
            completed: res.resbody.completed
          }
          resolve(result)
        }
      })
      .catch(() => {
        reject(defaultvalue)
      })
  })
}

/**
 * 个人活跃度
 * COM: basicchart.vue
 * STATUS: 2021-05-19   完成修改
 */
const personalactive = () => {
  // 临时值
  const defaultvalue = [
    {
      日期: '1',
      新建: 5,
      已完成: 12,
      执行中: 6,
      挂起: 22,
      冻结: 12
    },
    {
      日期: '2',
      新建: 12,
      已完成: 7,
      执行中: 12,
      挂起: 43,
      冻结: 2
    },
    {
      日期: '3',
      新建: 22,
      已完成: 38,
      执行中: 9,
      挂起: 8,
      冻结: 3
    },
    {
      日期: '4',
      新建: 3,
      已完成: 12,
      执行中: 12,
      挂起: 6,
      冻结: 3
    },
    {
      日期: '5',
      新建: 34,
      已完成: 87,
      执行中: 44,
      挂起: 9,
      冻结: 10
    }
  ]
  // 含有后台的时候，可以直接从vuex内获取部分信息
  const user = store.state.common.activeusers

  return new Promise(function(resolve, reject) {
    // 获取个人的前5日的任务
    getFunc('/query/taskonpersonal', { userid: user.id })
      .then(res => {
        if (res.resstatus === true) {
          const tempresult = []
          res.resbody.forEach(element => {
            const temp = {
              日期: element.beforeday,
              新建: element.content[0].value,
              已完成: element.content[2].value,
              执行中: element.content[1].value,
              挂起: element.content[3].value,
              冻结: element.content[4].value
            }
            tempresult.push(temp)
          })
          resolve(tempresult)
        }
      })
      .catch(() => {
        reject(defaultvalue)
      })
  })
}

/**
 * 按照任务类别分拆执行
 * COM: hometile.vue
 * STATUS: 完成修改
 * NOTICE: 2021-05-19   完成修改
 */
const worktabletask = () => {
  const defaultvalue = [
    { type: 'APIs', value: 66, total: 123 },
    { type: 'Jobs', value: 34, total: 233 },
    { type: 'Mock', value: 11, total: 76 }
  ]
  // 含有后台的时候，可以直接从vuex内获取部分信息
  const user = store.state.common.activeusers

  return new Promise(function(resolve, reject) {
    // 再从远程获取工作台前3的总结
    getFunc('/query/taskonworktable', { userid: user.id, count: 3 })
      .then(res => {
        if (res.resstatus === true) {
          resolve(res.resbody)
        }
      })
      .catch(() => {
        reject(defaultvalue)
      })
  })
}

/**
 * 返回昨日任务执行情况s
 * NOTICE: 借用timeline的展示
 */
const getYestdayTaskInfo = () => {
  const defaultvalue = []
  for (let index = 0; index < 10; index++) {
    // 从job取值
    const temp = {
      taskname: `自动化测试`,
      usetime: Math.floor(Math.random() * (1000 - 1) + 1)
    }
    defaultvalue.push(temp)
  }

  // 含有后台的时候，可以直接从vuex内获取部分信息
  const user = store.state.common.activeusers

  return new Promise(function(resolve, reject) {
    // 再从远程获取昨日任务top10耗时的总结
    getFunc('/query/taskonyestday', { userid: user.id, count: 3 })
      .then(res => {
        if (res.resstatus === true) {
          resolve(res.resbody)
        }
      })
      .catch(() => {
        reject(defaultvalue)
      })
  })
}
/**
 * 默认导出
 */
export { hero, personalactive, worktabletask, getYestdayTaskInfo }
